import React, { useState, useRef } from "react";
import { Form as FormikForm, Formik, FieldArray } from "formik";
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import FormField from "./FormField";
import { formSchema } from "./formSchema";
import SignatureCanvas from "react-signature-canvas";
import dataURLtoBlob from "blueimp-canvas-to-blob";
import { useMutation } from "react-query";
import { createInvoice } from "../api/invoiceApi";
import Modal from "./Modal";

const initialValues = {
  date: new Date(),
  name: "",
  lastname: "",
  email: "",
  city: "",
  postalCode: "",
  street: "",
  streetNumber: "",
  housingNumber: "",
  shoes: [],
};

// const initialValues = {
//   date: new Date(),
//   name: "Jan",
//   lastname: "Kowalski",
//   email: "jjsternik@gmail.com",
//   city: "Kabiczyce",
//   postalCode: "12-345",
//   street: "Maa",
//   streetNumber: "77",
//   housingNumber: "4",
//   shoes: [
//     {
//       model: "AirForce One",
//       size: 39,
//       price: 599.99,
//       count: 1,
//     },
//   ],
// };

export default function Form() {
  const [inputDate, setInputDate] = useState(new Date());
  const [isModal, setIsModal] = useState(false);
  const cavnasRef = useRef<any>();
  const mutation = useMutation({
    mutationFn: (data) => createInvoice(data),
    onSuccess: () => {
      setIsModal(true);
    },
    onError: (error) => {
      console.log(error);
    },
  });
  return (
    <div className="form">
      <Modal
        isOpen={isModal}
        closeModal={() => {
          setIsModal(false);
          window.location.reload();
        }}
      >
        <h2 className="modal__header">Wysano umow na maila!</h2>
        <p>Kliknij aby zamkn</p>
      </Modal>
      <h1 className="form__header--main">UMOWA KUPNA SPRZEDAY</h1>
      <p
        style={{
          marginBottom: "1rem",
        }}
      >
        <a href="/wzr_umowy.pdf" download>
          Pobierz wzr umowy
        </a>
      </p>
      <Formik
        validationSchema={formSchema}
        initialValues={initialValues}
        onSubmit={(v) => {
          if (!cavnasRef.current!.isEmpty()) {
            const data = new window.FormData();
            const blob = dataURLtoBlob(
              cavnasRef.current.toDataURL("image/png")
            );
            const json = JSON.stringify(v);
            data.append("json", json);
            data.append("signature", blob, "signature.png");
            mutation.mutate(data as any);
          }
        }}
      >
        {({ setFieldValue, values, errors }) => (
          <FormikForm>
            <div className="form__group">
              <h2 className="form__header">Dane sprzedajcego</h2>
              <FormField
                name="name"
                label="Imi"
                placeholder="Jan"
                disabled={mutation.isLoading}
              />
              <FormField
                name="lastname"
                label="Nazwisko"
                placeholder="Kowalski"
                disabled={mutation.isLoading}
              />
              <FormField
                name="email"
                label="Email"
                placeholder="example@gmail.com"
                disabled={mutation.isLoading}
              />
            </div>
            <div className="form__group">
              <h2 className="form__header">Adres</h2>
              <FormField
                name="city"
                label="Miasto"
                placeholder="Warszawa"
                disabled={mutation.isLoading}
              />
              <FormField
                disabled={mutation.isLoading}
                name="postalCode"
                label="Kod Pocztowy (00-000)"
                placeholder="01-358"
              />
              <FormField
                name="street"
                label="Ulica"
                placeholder="Polna"
                disabled={mutation.isLoading}
              />
              <FormField
                disabled={mutation.isLoading}
                name="streetNumber"
                label="Numer budynku"
                placeholder="12"
                type="string"
              />
              <FormField
                disabled={mutation.isLoading}
                name="housingNumber"
                label="Numer mieszkania (opcjonalne)"
                placeholder="3"
                type="number"
              />
            </div>
            <div className="form__group">
              <FieldArray
                name="shoes"
                render={({ push, remove }) => (
                  <>
                    <h2 className="form__header">
                      Buty
                      <button
                        onClick={() => {
                          !mutation.isLoading &&
                            push({
                              model: "",
                              size: null,
                              price: null,
                              count: 1,
                            });
                        }}
                        style={{
                          marginLeft: "2rem",
                        }}
                        type="button"
                        className="form__button form__button--small"
                        disabled={mutation.isLoading}
                      >
                        Dodaj <i className="bx bx-plus"></i>
                      </button>
                    </h2>

                    {values.shoes.length > 0 ? (
                      values.shoes.map((shoe, index) => (
                        <div key={index}>
                          <FormField
                            disabled={mutation.isLoading}
                            name={`shoes[${index}].model`}
                            label="Model"
                          />
                          <FormField
                            disabled={mutation.isLoading}
                            name={`shoes[${index}].size`}
                            label="Rozmiar"
                            type="number"
                          />
                          <FormField
                            disabled={mutation.isLoading}
                            name={`shoes[${index}].price`}
                            label="Cena (z)"
                            type="number"
                          />
                          <FormField
                            disabled={mutation.isLoading}
                            name={`shoes[${index}].count`}
                            label="Ilo par"
                            type="number"
                          />
                          <button
                            className="form__button form__button--small form__button--danger"
                            type="button"
                            disabled={mutation.isLoading}
                            onClick={() => {
                              !mutation.isLoading && remove(index);
                            }}
                          >
                            Usu <i className="bx bxs-trash-alt"></i>
                          </button>
                          <div className="form__divider"></div>
                        </div>
                      ))
                    ) : (
                      <p className="form__error">
                        Musisz doda przynajmniej jedne buty!
                      </p>
                    )}
                  </>
                )}
              />
            </div>
            <div className="form__group">
              <label htmlFor="date" className="form__label">
                Data
              </label>
              <DatePicker
                disabled={mutation.isLoading}
                className="form__input"
                placeholderText="Wybierz date..."
                dateFormat="dd/MM/yyyy"
                selected={inputDate}
                id="date"
                popperProps={{
                  strategy: "fixed",
                }}
                onSelect={(d) => {
                  setFieldValue("date", d, false);
                  setInputDate(d);
                }}
                onChange={(d) => {
                  setFieldValue("date", d, false);
                  setInputDate(d as any);
                }}
              />
              <label htmlFor="canvas" className="form__label">
                Podpis (uyj myszki)
              </label>
              <SignatureCanvas
                penColor="black"
                ref={cavnasRef as any}
                canvasProps={{
                  id: "canvas",
                  className: "form__canvas",
                  style: mutation.isLoading
                    ? {
                        border: "2px solid #575252",
                      }
                    : {},
                }}
              />
            </div>
            <button
              type="button"
              className="form__button form__button--secondary"
              style={{
                marginRight: "1rem",
              }}
              onClick={() => {
                cavnasRef.current!.clear();
              }}
            >
              Resetuj podpis
            </button>
            <button
              type="submit"
              className="form__button"
              disabled={mutation.isLoading}
            >
              Wylij
            </button>
          </FormikForm>
        )}
      </Formik>
    </div>
  );
}
