import * as yup from "yup";

export const formSchema = yup.object({
  date: yup.date().required("Data jest wymagana!"),
  name: yup
    .string()
    .required("Imi jest wymagane!")
    .min(3, "Imi musi mie min. 3 znaki!"),
  lastname: yup
    .string()
    .required("Nazwisko jest wymagane!")
    .min(2, "Nazwisko musi mie min. 2 znaki!"),
  email: yup
    .string()
    .required("Email jest wymagany!")
    .email("Podano niepoprawny adres email!"),
  postalCode: yup
    .string()
    .required("Kod pocztowy jest wymagany!")
    .matches(/^[0-9]{2}-[0-9]{3}$/, "Podany kod pocztowy nie jest poprawny!"),
  city: yup
    .string()
    .required("Miasto jest wymagane!")
    .min(2, "Za krtka nazwa miasta!"),
  street: yup
    .string()
    .required("Ulica jest wymagana!")
    .min(2, "Nazwa ulicy musi mie min. 2 znaki!"),
  streetNumber: yup
    .string()
    .required("Numer budynku jest wymagany!")
    .min(1, "Niepoprawny numer budynku!"),
  housingNumber: yup.number().min(1, "Niepoprawny numer mieszkania!"),
  shoes: yup
    .array()
    .of(
      yup.object().shape({
        model: yup.string().required("Model buta jest wymagany!"),
        size: yup
          .number()
          .required("Rozmiar buta jest wymagany!")
          .min(1, "Niepoprawny rozmiar buta!")
          .max(70, "Niepoprawny rozmiar buta!"),
        price: yup
          .number()
          .required("Cena buta jest wymagana!")
          .min(1, "Niepoprawna cena buta!"),
        count: yup
          .number()
          .required("Ilo par jest wymagana!")
          .min(1, "Niepoprawna ilo par!"),
      })
    )
    .min(1, "Musisz doda przynajmniej jedne buty!"),
});
